// colors (when hovering)
[data-dark="false"] {
  --primary:  rgb(112, 112, 112);
  --secondary: #7dd3fc;
  --text: #000000;
  --background: #ffffff;
  --background-alt: #fafafa;
  --light-gray: #e0e0e0;
  --gray: #808080;
  --dark-gray: #404040;
  --overlay: #00000020;
}
[data-dark="true"] {
  --primary:hsl(44, 100%, 70%);
  --secondary: #075985;
  --text:  #000000; 
  --background:   #ffffff;
  --background-alt: #1c1c1c;
  --light-gray: #404040;
  --gray: #808080;
  --dark-gray: #b0b0b0;
  --overlay: #ffffff10;
}
// yellow: hsl(44, 100%, 70%)
:root {
  // font families
  --title: "Rochester","Papyrus","serif","Arial", "Helvetica","sans-serif";
  --heading: "Rochester", "serif","Arial","Helvetica","sans-serif","Barlow";
  --heading_nav: "serif","Arial","Helvetica","sans-serif","Barlow";
  --body: "serif", "Arial", "Helvetica", "sans-serif", "Barlow";
  --code: "Roboto Mono", monospace;
  --site-color-01: hsl(44, 100%, 70%);
  --site-color-01-hover: rgb(112, 112, 112);

  /*
    --font-01-header: Rochester,Papyrus, serif, Arial, Helvetica, sans-serif;
    --font-02-nav: serif, Arial, Helvetica, sans-serif;
    --font-03-body: serif, Arial, Helvetica, sans-serif;
    --border-main:  0.4rem solid var(--site-color-01);
  */

  // font sizes
  --small: 0.1rem;
  --large: 1.2rem;
  --xl: 1.4rem;
  --xxl: 1.6rem;
  --xxxl: 6rem;

  // font weights
  --thin: 200;
  --regular: 400;
  --semi-bold: 500;
  --bold: 600;

  // text line spacing
  --spacing: 2;
  --compact: 1.5;

  // effects
  --rounded: 3px;
  --shadow: 0 0 10px 0 var(--overlay);
  --transition: 0.2s ease;
}
